import { useState, useEffect } from 'react'
import { inject, observer } from 'mobx-react'
import { formatAddress, eqInNumber } from '../../utils/utils'
import { useAlert } from 'react-alert'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import Button from '../../components/Button/Button'
import apiProxy from '../../model/ApiProxy'
import timeLogo from './img/time.svg'
import './airdrop.less'
function AriDrop({ lang, wallet = {} }) {
  const [balance, setBalance] = useState(0);
  const [disabled, setDisabled] = useState(true);
  const alert = useAlert();
  const [info, setInfo] = useState({})
  const getBalance = async () => {
    if (wallet.isConnected()) {
      let res = await apiProxy.request('getDeriBalance', [wallet.detail.chainId, wallet.detail.account])
      setBalance(res)
    }
  }
  const getInfo = async () => {
    if (wallet.isConnected()) {
      let info = await apiProxy.request('getUserInfoAllForAirDrop', [wallet.detail.account])
      setInfo(info)
      setDisabled(!info.valid)
      let timestamp = new Date()
      if(timestamp.getTime() >= 1640253600000){
        setDisabled(true)
      }
    }

  }

  const click = async () => {
    if (!info.valid) {
      alert.error(lang['no-deri-to-claim']);
      return;
    }
    if (!eqInNumber(wallet.detail.chainId, info.chainId)) {
      alert.error(lang['please-switch-to-BSC-to-claim-deri'])
      return;
    }
    let res = await apiProxy.request('mintAirdrop', [info.chainId, wallet.detail.account], { includeResponse: true })
    if (!res.success) {
      alert.error(lang['claim-failed'])
    }
    if(res.success){
      setDisabled(true)
    }
    getBalance()
  }
  useEffect(() => {
    getBalance()
    getInfo()
  }, [wallet,wallet.detail])
  return (
    <div className='airdrop-box'>
      <div className='airdrop-info'>
        <div className='airdrop-info-box'>
          <div className='airdrop-title'>
            {lang['title']}
          </div>
          <div className='wallet-address'>
            <span className='blue-logo'></span>
            {formatAddress(wallet.detail.account)}
          </div>
          <div className='wallet-balance'>
            {lang['wallet-balance']} : <DeriNumberFormat value={balance} decimalScale={0} thousandSeparator={true} /> DERI
          </div>
          <div className='airdrop-time'>
            <div className='time-logo'>
              <img src={timeLogo} alt='' />
            </div>
            <div className='time-text'>
              <div className='time-title'>
                <div>
                  {lang['start-time']}
                </div>
                <div>
                  {lang['expire-time']}
                </div>
              </div>
              <div>
                <div>
                  2021-12-23 10:00:00 UTC
                </div>
                <div>
                  2021-12-25 10:00:00 UTC
                </div>
              </div>
            </div>
          </div>
          <div className='claim'>
            <Button fontWeight='bold' disabled={disabled} label={lang['claim']} width='277' height='72' fontSize='32' onClick={click} />
          </div>
        </div>
      </div>
    </div>
  )
}
export default inject('wallet', 'poolManager')(observer(AriDrop))